import React from 'react'
import PropTypes from 'prop-types'
import styled, { css } from 'styled-components'
import { Link } from 'gatsby'

import selected from './selected.svg'
import unselected from './unselected.svg'

const link = css`
  display: inline-block;
  background-color: transparent;
  font-weight: normal;
  height: 2.4rem;
  font-size: 1.6rem;
  line-height: 1.25;
  text-align: center;
  border-width: 0.2rem;
  border-style: solid;
  border-image: url(${unselected}) 2 stretch;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 0 ${props => props.theme.space[3]};

  &:focus,
  &:active {
    background-color: ${props => props.theme.colors.primary};
    border-image: url(${selected}) 2 stretch;
  }
`

const Foo = styled.button`
  ${link};
`
const Bar = styled(Link)`
  ${link};
`

const Baz = styled.a`
  ${link};
`

const propTypes = {
  name: PropTypes.string,
  url: PropTypes.string,
}

const defaultProps = {
  name: 'Action name',
  url: null,
}

const Action = ({ name, url, ...props }) => {
  if (!url) {
    return <Foo {...props}>{name}</Foo>
  }

  const internal = /^\/(?!\/)/.test(url)

  if (internal) {
    return (
      <Bar to={url} {...props}>
        {name}
      </Bar>
    )
  }

  return (
    <Baz href={url} target="_blank" rel="noreferrer noopener" {...props}>
      {name}
    </Baz>
  )
}

Action.propTypes = propTypes
Action.defaultProps = defaultProps

export default Action
